import { Component } from '@angular/core';
import { NavController, LoadingController } from 'ionic-angular';
import { Geolocation } from '@ionic-native/geolocation';
import { HttpClient } from '@angular/common/http';

import { HTTP } from '@ionic-native/http';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  public error : any = '';
  public p : any = [];
  public mode : any = 0;
  public myname : any = '';
  public started : any = 0;

  constructor(public navCtrl: NavController, public loadingCtrl : LoadingController, private geolocation: Geolocation, private http: HttpClient, public nhttp : HTTP) {
    this.p = { email : '', pass : '', empcode : '', timezn : '', locn : 'Unknown', lat : '', lng : '', me : 0};
    
  }

  ionViewDidLoad() {
     let that = this;
     let me = window.localStorage.getItem('me');
     if (!me) me='';
     if (me) {
       this.mode=1;
       this.myname = window.localStorage.getItem('myname');
       this.pinme();
     }
     else this.mode=0;
  }

  checkeml()
  {
         this.error='';
        let that = this;
	this.http.get('http://jaydenkur.com.au/giovenco/server/checkeml.php?eml='+this.p.email+'&pwd='+this.p.pass).subscribe(data	=>	{	
       		let s = JSON.stringify(data);
                let d = JSON.parse(s);
                if (d.num) {
                  window.localStorage.setItem('myname',d.myname);
                  window.localStorage.setItem('me',d.num); 
                  this.mode=1; 
                  this.myname=d.myname;
                  that.pinme();
                }
                else {
                  this.error='Incorrect - Please Try Again';
                }
        });


  }

  pinme()
  { let that=this;
    let d = new Date();
    let pp = d.toString();
    let a = pp.indexOf('GMT');
    
    this.p.timezn = pp.substr(a);
    this.p.me = window.localStorage.getItem('me');
this.geolocation.getCurrentPosition().then((resp) => {
   that.p.lat = resp.coords.latitude;
   that.p.lng = resp.coords.longitude;
	this.http.get('http://jaydenkur.com.au/giovenco/server/checklocn.php?me='+window.localStorage.getItem('me')+'&lat='+that.p.lat+'&lng='+that.p.lng).subscribe(data	=>	{	
       		let s = JSON.stringify(data);
                let d = JSON.parse(s);
                that.p.locn = d.locn;
                that.started = d.started;
        });


}).catch((error) => {
        that.p.lat=0;
        that.p.lng=0;
	this.http.get('http://jaydenkur.com.au/giovenco/server/checklocn.php?me='+window.localStorage.getItem('me')+'&noloc=1').subscribe(data	=>	{	
       		let s = JSON.stringify(data);
                let d = JSON.parse(s);
                that.p.locn = d.locn;
                that.started = d.started;
        }); }
 );
}

 logout()
 { window.localStorage.removeItem('me');
   window.localStorage.setItem('myname','');
   this.mode=0;
 }
 
 startShift()
 {     let that = this;
       let loading = this.loadingCtrl.create({
         content: 'Please wait...'
       });
       loading.present();
       let url = 'http://jaydenkur.com.au/giovenco/server/startshift.php';

         this.nhttp.post(url, that.p, {})
         .then(data => {
            loading.dismiss();
 	    that.started=1;
         })
         .catch(error => {
            alert(JSON.stringify(error));
            loading.dismiss();
     
         });
 }
  
 endShift()
 {
     let that = this;
       let loading = this.loadingCtrl.create({
         content: 'Please wait...'
       });
       loading.present();
       let url = 'http://jaydenkur.com.au/giovenco/server/endshift.php';

         this.nhttp.post(url, that.p, {})
         .then(data => {
            loading.dismiss();
 	    that.started=0;
         })
         .catch(error => {
            alert(JSON.stringify(error));
            loading.dismiss();
     
         });

 }

}



